// 
import HelloWorld from './helloWorld'
import VueAntdDynamicTable from './vueAntdDynamicTable'
// 
const components = [
  HelloWorld,
  VueAntdDynamicTable
]

//  install  Vue  use 
const install = function (Vue) {
  // // 
  // if (install.installed) return
  // 
  components.map(component => Vue.component(component.name, component))
}
// // 
// if (typeof window !== 'undefined' && window.Vue) {
//   install(window.Vue)
// }
export default {
  //  install Vue.use() 
  install,
  // 
  HelloWorld,
  VueAntdDynamicTable
}